import MapController from 'src/controller/pixi/MapController.js';
import { testPositionRangeError, rangeErrorString } from 'test/utils/PositionTestHelper.js';

var MAPCONTROLLER_TEST_TAG = '|MAP CONTROLLER|';

let mapController = new MapController();

QUnit.test(MAPCONTROLLER_TEST_TAG + 'set-map-view-position-valid', function (assert) {
	//	Smallest global position valid
	mapController.setMapViewPosition(0, 0);
	assert.equal(mapController.mapModel.mapViewStartX, 0);
	assert.equal(mapController.mapModel.mapViewStartY, 0);

	var lowestRange = -mapController.mapModel.halfTileCountFloored;
	//	Smallest relative position (minus half the tilecount)
	mapController.setMapViewPosition(lowestRange, lowestRange);
	assert.equal(mapController.mapModel.mapViewStartX, lowestRange);
	assert.equal(mapController.mapModel.mapViewStartY, lowestRange);

	var tileCountZeroIndexedLen = mapController.mapModel.tileCount - 1;
	//	Largest global position valid
	mapController.setMapViewPosition(tileCountZeroIndexedLen, tileCountZeroIndexedLen);
	assert.equal(mapController.mapModel.mapViewStartX, tileCountZeroIndexedLen);
	assert.equal(mapController.mapModel.mapViewStartY, tileCountZeroIndexedLen);
}
);

QUnit.test(MAPCONTROLLER_TEST_TAG + 'set-map-view-position-invalid', function (assert) {
	var justUnderRange = -mapController.mapModel.halfTileCountFloored - 1;
	var lowestRange = -mapController.mapModel.halfTileCountFloored;

	//	Independant x under range (Map view should allow halfTileCountFloored offset)
	testPositionRangeError(assert, mapController.mapModel.setMapViewPosition, justUnderRange, lowestRange, new RangeError(rangeErrorString(justUnderRange, lowestRange)));
	//	Independant y under range
	testPositionRangeError(assert, mapController.mapController.mapModel.setMapViewPosition, lowestRange, justUnderRange, new RangeError(rangeErrorString(lowestRange, justUnderRange)));
	//	X and Y under range
	testPositionRangeError(assert, mapController.mapModel.setMapViewPosition, justUnderRange, justUnderRange, new RangeError(rangeErrorString(justUnderRange, justUnderRange)));

	// "Local tile pos for conversion not relative to the map view"
	//	Independant x over range
	testPositionRangeError(assert, mapController.mapModel.setMapViewPosition, mapController.mapModel.mapSizeX, 0, new RangeError(rangeErrorString(mapController.mapModel.mapSizeX, 0)));
	//	Independant y over range
	testPositionRangeError(assert, mapController.mapModel.setMapViewPosition, 0, mapController.mapModel.mapSizeY, new RangeError(rangeErrorString(0, mapController.mapModel.mapSizeY)));
	//	X and Y over range
	testPositionRangeError(assert, mapController.mapModel.setMapViewPosition, mapController.mapModel.mapSizeX, mapController.mapModel.mapSizeY, new RangeError(rangeErrorString(mapController.mapModel.mapSizeX, mapController.mapModel.mapSizeY)));

	assert.expect(6); //  Expect Error assertions for every test
}
);
