//  Runs the position function passed with x and y params
//  catches any error and checks the error message starts with the expected error message
export function testPositionRangeError (assert, func, x, y, expectedException) {
	var passed = false;
	var message = 'Checking position ' + x + ',' + y + ' throws an error';
	try {
		func(x, y);
	} catch (err) {
		passed = err.message.startsWith(expectedException.message);
		message = 'Checking position ' + x + ',' + y + ' error message: ' + err.message + ' starts with: ' + expectedException.message;
	}

	// Ensure our catch assertion was performed, passing the message if a failed
	assert.ok(passed, message);
}

export function rangeErrorString (posX, posY) {
	return 'Position not in overworld: ' + (posX) + ' ' + (posY);
}
