// Helper class for position calculation and verification
export class MapPositionHelper {
	// Must have a map model to check against
	constructor (mapModel) {
		this.mapModel = mapModel;
	}

	//	Check whether or not this position is a view-relative one using x/y from 0 - tileCount
	isPositionRelativeToView (x, y) {
		if (x < this.mapModel.tileCount && x >= 0 && y < this.mapModel.tileCount && y >= 0) return true;
		return false;
	}

	//	Check whether or not a GLOBAL POSITION is within our map view window
	isPositionInMapView (globalX, globalY) {
		if (globalX < (this.mapModel.mapViewStartX + this.mapModel.tileCount) &&
				globalX >= this.mapModel.mapViewStartX &&
				globalY < (this.mapModel.mapViewStartY + this.mapModel.tileCount) &&
				globalY >= this.mapModel.mapViewStartY) {
			return true;
		} else {
			return false;
		}
	}

	// Checks whether the position is valid in the range of 0 - < mapSizeXorY
	isPositionInMap (globalX, globalY) {
		// < for max range as map tiles are 0-indexed
		if (globalX < this.mapModel.mapSizeX &&
				globalX >= 0 &&
				globalY < this.mapModel.mapSizeY &&
				globalY >= 0) {
			return true;
		} else {
			return false;
		}
	}

	//	We only view the map through our view window,
	//	This static adjusts a local position 0-tileCount (window co-ord), to a real position on the map
	localTilePosToGlobal (localX, localY) {
		//	Ensure these are view tile co-ordinates
		if (!this.isPositionRelativeToView(localX, localY)) {
			throw new RangeError('Local tile pos for conversion not relative to the map view: ' + localX + ',' + localY);
		} else {
			//	Shift each of these positions by the starting position of our map view
			localX += this.mapModel.mapViewStartX;
			localY += this.mapModel.mapViewStartY;

			//	Double check we're returning a sane global map position
			if (!this.isPositionInMap(localX, localY)) {
				throw new RangeError('Local tile pos for conversion plus offset, not in the global map: ' + localX + ',' + localY);
			} else {
				return [localX, localY];
			}
		}
	}

	//	We only view the map through our view window,
	//	This static adjusts the global position (with relative offset) to a value relative to the grid view
	globalTilePosToLocal (globalX, globalY) {
		var mapViewStartX = this.mapModel.mapViewStartX;
		var mapViewStartY = this.mapModel.mapViewStartY;

		if (!this.isPositionInMap(globalX, globalY)) {
			throw new RangeError('Global tile pos for conversion not in the overworld: ' + globalX + ',' + globalY);
		} else {
			if (globalX < mapViewStartX ||
					globalY < mapViewStartY ||
					globalX > mapViewStartX + this.mapModel.tileCount ||
					globalY > mapViewStartY + this.mapModel.tileCount) {
				throw new RangeError('Global tile pos for conversion not in the local view: ' + globalX + ',' + globalY);
			}
			return [globalX - mapViewStartX, globalY - mapViewStartY];
		}
	}

	//	Converts tile coords from 0,0 - X,X based on tilecount to a Pixi stage pixel position
	//		-This takes a global position (say the map is 20 tiles, so from 0-19)
	//		-that position is then converted to a pixel amount based:
	//				--tile size
	//				--how many tiles are in the UI
	//				--where the view window is
	//		-Returns an array of len 2 [x,y]m there
	tileCoordToPixiPos (xRelative, yRelative) {
		//	Sanity check
		if (!this.isPositionRelativeToView(xRelative, yRelative)) throw new RangeError('Tile-to-Pixi conversion, tile position invalid!');

		var posX = (xRelative * this.mapModel.tileSize);
		var posY = (yRelative * this.mapModel.tileSize);

		// console.log('Tilesize: '+tileSize+'Co-ord pos: '+x_relative+' '+y_relative+'\n'+'Pixi pos: '+pos_x+' '+pos_y);

		return [posX, posY];
	}

	pixiPosToTileCoord (x, y) {
		//	Sanity check for input co-ords
		var furthestPos = this.mapModel.tileSize * this.mapModel.tileCount;
		if (x < 0 || x > furthestPos || y < 0 || y > furthestPos) throw new RangeError('Pixi-to-Tile conversion, pixi position invalid!');

		//	Round down so clicks on the upper-half of tiles still convert correctly
		var clientX = Math.floor(x / this.mapModel.tileSize);
		var clientY = Math.floor(y / this.mapModel.tileSize);

		// Sanity check to make sure we can't click over the boundary
		var zeroIndexedTileCount = this.mapModel.tileCount - 1;
		if (clientX > zeroIndexedTileCount) clientX = zeroIndexedTileCount;
		if (clientY > zeroIndexedTileCount) clientY = zeroIndexedTileCount;

		// console.log('PIXI pos: '+x+' '+y+'\n'+'Tile pos: '+clientX+' '+clientY);
		return [clientX, clientY];
	}
}

export default MapPositionHelper;
